	module.exports = {"data":{"markdownRemark":{"html":"<p>You can customize your program's response targets by configuring the number of business days.</p>\n<p>To customize your program's response target:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Response Target</strong>.</li>\n<li>Input the response target in days for your program under the <strong>Target</strong> column.</li>\n<li>\n<p><em>(Optional)</em> Set targets for time to resolution by severity. To set targets by severity:</p>\n<ul>\n<li>Select the check box for <strong>Advanced: Set target by severity</strong>.</li>\n<li>Input the target in days for each severity level.</li>\n</ul>\n</li>\n</ol>\n<p><em>Keep in mind that: it's recommended that your targets fall within the Recommended and Response Standard times.</em></p>\n<table>\n<thead>\n<tr>\n<th>Time</th>\n<th>Recommended</th>\n<th>Response Standard</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Time to first response</td>\n<td>1 day</td>\n<td>5 days</td>\n</tr>\n<tr>\n<td>Time to triage</td>\n<td>2 days</td>\n<td>10 days</td>\n</tr>\n<tr>\n<td>Time to bounty</td>\n<td>1 day (after triage)</td>\n<td>N/A</td>\n</tr>\n<tr>\n<td>Time to resolution</td>\n<td>30 days</td>\n<td>N/A</td>\n</tr>\n</tbody>\n</table>\n<ul>\n<li>To edit your program's response targets, you need to have program permissions.</li>\n<li>Modified targets will be applied to reports created after the modification.</li>\n<li>Reports created before modification will have previous response targets applied.</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/response-targets-c48ea61a40e117ece01f450711c86e01-36e4b.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 93.62880886426592%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"response targets\"\n        title=\"\"\n        src=\"/static/response-targets-c48ea61a40e117ece01f450711c86e01-e7ffa.png\"\n        srcset=\"/static/response-targets-c48ea61a40e117ece01f450711c86e01-49813.png 125w,\n/static/response-targets-c48ea61a40e117ece01f450711c86e01-dc8a5.png 250w,\n/static/response-targets-c48ea61a40e117ece01f450711c86e01-e7ffa.png 500w,\n/static/response-targets-c48ea61a40e117ece01f450711c86e01-dda24.png 750w,\n/static/response-targets-c48ea61a40e117ece01f450711c86e01-c5f18.png 1000w,\n/static/response-targets-c48ea61a40e117ece01f450711c86e01-36e4b.png 1444w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/programs/setting-response-targets.html","id":"programs/setting-response-targets","title":"Setting Response Targets","description":null}}},"pathContext":{}}

